import React, { useState, useEffect } from 'react';
import { TextField, Button, Typography, Paper } from '@material-ui/core';
import FileBase from "react-file-base64";
import useStyles from './style.js';
import { useDispatch } from 'react-redux'
import {createPost, updatePost} from '../../actions/posts';
import { useSelector } from 'react-redux';

function Form({ currentId, setCurrentId }) {
    const classes = useStyles();
    const dispatch = useDispatch();
    const post = useSelector((state) => currentId ? state.posts.find(p => p._id === currentId) : null)
    const [postData, setData] = useState({creator: "", title: "", message: "", tags: "", selectedFile: ""})

    useEffect(() => {
      if(post) setData(post)
    }, [post]);

    const handleSubmit = (e) => {
      e.preventDefault();
      if(currentId){
        dispatch(updatePost(currentId, postData))  
      } else {
      dispatch(createPost(postData))
    }
      clear();
    }

    const clear = () => {
      setCurrentId(null);
      setData({creator: "", title: "", message: "", tags: "", selectedFile: ""})
    }

  return (
    <Paper className={classes.paper}>
      <form autoComplete='off' noValidate className={`${classes.root} ${classes.form}`} onSubmit={handleSubmit}>
        <Typography variant='h6'>{currentId ? 'Editing' : 'Creating'} a memory</Typography>
        <TextField 
          name="creator" 
          variant='outlined' 
          label="Creator" 
          fullWidth
          value={postData.creator}
          onChange={(e) => { setData({ ...postData, creator: e.target.value }) }} 
        />
        <TextField 
          name="title" 
          variant='outlined' 
          label="Title" 
          fullWidth
          value={postData.title}
          onChange={(e) => { setData({ ...postData, title: e.target.value }) }} 
        />
        <TextField 
          name="message" 
          variant='outlined' 
          label="Message" 
          fullWidth
          value={postData.message}
          onChange={(e) => { setData({ ...postData, message: e.target.value }) }} 
        />
        <TextField 
          name="tags" 
          variant='outlined' 
          label="Tags" 
          fullWidth
          value={postData.tags}
          onChange={(e) => { setData({ ...postData, tags: e.target.value.split(',') }) }} 
        />
        <div className={classes.fileInput}>
          <FileBase 
            type="file"
            multiple={false}
            onDone={({base64}) => {
              setData({ ...postData, selectedFile: base64 })
            }}
          />
        </div>
        <Button className={classes.buttonSubmit} variant="contained" color="primary" size="large" type="submit" fullWidth>Submit</Button>
        <Button variant="contained" color="secondary" size="small" onClick={clear} fullWidth>Clear</Button>
      </form>
    </Paper>
  )
}

export default Form